<template>
  <div class="login-box-div">
    <div class="juzhong">
      <el-form
        :model="loginInfo"
        :rules="rules"
        ref="loginForm"
        class="demo-ruleForm"
      >
        <div class="login-box-img">
          <img src="../../assets/img/none.png" alt="" />
        </div>
        <el-form-item prop="username">
          <div class="login-box-input">
            <el-input
              type="text"
              placeholder="Email / Name"
              v-model="loginInfo.username"
            >
            </el-input>
          </div>
        </el-form-item>
        <el-form-item prop="password">
          <div class="login-box-input">
            <el-input
              type="password"
              placeholder="Password"
              v-model="loginInfo.password"
            >
            </el-input>
          </div>
        </el-form-item>
        <el-form-item>
          <div class="login-box-span">
            <div>
              还没账号?
              <span class="register-btn" @click="gotoRegister">立即注册</span>
            </div>
            <span class="forget-password-btn" @click="gotoForget"
              >忘记密码?</span
            >
          </div>
          <div class="login-box-button">
            <el-button class="login-button" @click="gotoLogin" type="info"
              >登 入</el-button
            >
          </div>
          <div class="login-box-button">
            <el-button class="login-button-home" @click="gotoMain" type="info"
              >返回首页</el-button
            >
          </div>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loginInfo: {
        username: "",
        password: "",
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名或邮箱", trigger: "blur" },
          {
            min: 2,
            max: 28,
            message: "长度在 2 到 28 个字符",
            trigger: ["blur", "change"],
          },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 6,
            max: 18,
            message: "长度在 6 到 18 个字符",
            trigger: ["blur", "change"],
          },
        ],
      },
    };
  },
  methods: {
    gotoMain(){
      this.$router.push("/main");
    },
    gotoRegister() {
      this.$router.push("/register");
    },
    gotoForget() {
      this.$router.push("/forget");
    },
    gotoLogin() {
      this.$refs.loginForm.validate(async (valid) => {
        if (valid) {
          const res = await this.$api.user.login(this.loginInfo);
          if (res.data.code === 200) {
            // 登录成功
            // 存token
            window.sessionStorage.setItem("token", res.data.data.token);
            this.$store.dispatch("asyncUpdatedToken",res.data.data)
            // 消息提示
            this.$message({
              message: "恭喜你，登陆成功",
              type: "success",
            });
            // 跳转home
            this.$store.dispatch("asyncUpdatedActiveIndex","/main")
            this.$router.push("/main");
            window.sessionStorage.setItem(
              "userInfo",
              JSON.stringify(res.data.data)
            );
          } else {
            this.$message.error("账号或密码错误");
          }
        }
        return;
      });
    },
  },
};
</script>

<style scoped>
.juzhong {
  width: 280px;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
img {
  width: 85px;
  height: 85px;
}
.login-box-div {
  margin-top: 160px;
}
.login-box-img {
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}
.login-box-input {
  margin: auto;

  width: 280px;
}
.login-box-span {
  margin: auto;
  display: flex;
  justify-content: space-between;
  width: 280px;
  color: #666666;
}
.login-box-button {
  /* margin: auto;
  display: flex;
  justify-content: center; */
}
.login-button {
  width: 280px;
  background-color: #eeeeee;
  border: solid 1px #dcdfe6;
  color: #666666;
}
.login-button-home{
  margin-top: 10px;
  width: 280px;
  background-color: #eeeeee;
  border: solid 1px #dcdfe6;
  color: #666666;
}
.login-button-home:hover {
  background-color: #e9b5fa;
  border: solid 1px #e9b5fa;
  color: white;
}
.login-button:hover {
  background-color: #e9b5fa;
  border: solid 1px #e9b5fa;
  color: white;
}
.register-btn {
  color: #0090f7;
  cursor: pointer;
}
.forget-password-btn {
  color: #0090f7;
  cursor: pointer;
}
</style>